export default {
	data() {
		return {
			logoImage: "../../../static/user/logo_null.png",
		}
	},
	methods: {
		logo() {
			var _this = this;
			uni.chooseImage({
				count: 1,
				sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
				sourceType: ['album'], //从相册选择
				success: function(res) {
					_this.logoImage = res.tempFilePaths[0];
				}
			});
		},
		nickname() {
			uni.navigateTo({
				url: "nickname",
			})
		}
	}
}
